.App{
  background-color: #4BBCF4;
}

a:hover{
  color:rgb(228, 52, 75) !important;
  cursor: pointer;
}

p {
  font-family: 'Open Sans', sans-serif;
}

h2,h1,h3,h5,div{
  font-family: 'Nexa-Bold';
}


h3 span:hover{
  display: inline-block;
  animation: headline 2s ;

}


@keyframes headline {
from {
  transform: rotate(0deg);
  color:rgb(228, 52, 75);
}
to {
  transform: rotate(360deg);
  }
}



@font-face {
  font-family: 'Nexa-Bold';
  src: url('./assets/fonts/Nexa-Bold.otf');
} 